import React, { Component } from "react"

import { IconContext } from "react-icons";
import { FaGithub, FaKaggle, FaLinkedin } from 'react-icons/fa';
import { SiGooglescholar } from 'react-icons/si'
import { IoMdMail } from 'react-icons/io'

import "../styles/nav.scss"

import Logo from "../images/logo.png"
import Toggle from "./toggle"

class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      toggled: false,
    };
  }

  componentDidMount() {
    this.setState({
      nav: document.querySelector("#inner-header"),
      toggled: localStorage.getItem("data-theme") === "dark" ? true : false,
    })
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = (event) => {
    if (window.scrollY > 80){
      this.state.nav.classList.add('fixed')
    }
    else{
      this.state.nav.classList.remove('fixed')
    }
  }

  handleClick = () => {
    this.setState({ toggled: !this.state.toggled });
    if(this.state.toggled){
      document.documentElement.setAttribute("data-theme", "light")
      localStorage.setItem("data-theme", 'light')
    }else{
      document.documentElement.setAttribute("data-theme", "dark")
      localStorage.setItem("data-theme", 'dark')
    }
  }

  render() {
    return (
      <header className="nav">
        <div className="blank"></div>
        <div className="fake">
          <div className="center">
            <div className="inner-center">
              <h1 className="name">
                Parsa
              </h1>
              <h1 className="last">
                Sam
              </h1>
            </div>
          </div>
        </div>
        <div id="inner-header">
          <div className="left">
            <div className="inner-left">
              {/* <a href="psam.xyz">About</a>
              <a href="psam.xyz">Education</a>
              <a href="psam.xyz">Publications</a>
              <a href="psam.xyz">Projects</a> */}
            </div>
          </div>
          <div className="center">
            <div className="inner-center">
              <div className="logo">
                <img alt="" src={Logo}/>
              </div>
              <h1 className="name">
                Parsa
              </h1>
              <h1 className="last">
                Samadnejad
              </h1>
            </div>
          </div>
          <div className="right">
            <div className="inner-right">
              {/* <a href="psam.xyz">Contact</a>
              <a href="psam.xyz">Blog</a>
              <a href="psam.xyz">Vocab</a> */}
              <div className="toggle-container">
                <Toggle toggled={this.state.toggled} onClick={this.handleClick}/>
              </div>
            </div>
          </div>
        </div>
        <div className="links">
          <IconContext.Provider value={{ size: "1.1em" }}>
            <a href="mailto:parsasam1378@gmail.com" target="_blank" rel="noreferrer" className="icon">
              <IoMdMail/>
            </a>
          </IconContext.Provider>
          <IconContext.Provider value={{ size: "1.1em" }}>
            <a href="https://github.com/troddenspade" target="_blank" rel="noreferrer" className="icon">
              <FaGithub/>
            </a>
          </IconContext.Provider>
          {/* <IconContext.Provider value={{ size: "1.1em" }}>
            <div className="icon">
              <FaKaggle/>
            </div>
          </IconContext.Provider>
          <IconContext.Provider value={{ size: "1.1em" }}>
            <div className="icon">
              <FaLinkedin/>
            </div>
          </IconContext.Provider>
          <IconContext.Provider value={{ size: "1.1em" }}>
            <div className="icon">
              <SiGooglescholar/>
            </div>
          </IconContext.Provider> */}
        </div>
      </header>
    )
  }
}

export default Nav